<template>
    <div class="text-left mb-10">
        <h3 class="text-xl">{{text}}</h3>
        <div class="font-mono text-gray-400">{{timestamp}}</div>
    </div>
</template>

<script>
import moment from 'moment'

export default {
    name: 'DataTitle',
    props: ['text', 'dataDate'],
    computed: {
        timestamp: function() {
            return moment(this.dataDate).format('D-MM-YYYY, H:mm:ss')
        }
    }
}
</script>